@import url(https://fonts.googleapis.com/css2?family=Fredoka:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --red: #ff3e3e;
  --honeydew:#f1faee;
  --neutral: #f3f5f2;
  --light-blue: #a8dadc;
  --medium-blue: #5cb2ff;
  --dark-blue: #004cff;
  --almost-black: #;
  --decor-font: 'Secular One', Arial, Helvetica, sans-serif;
  --fg-color: #a4b1cd
}

* {
  box-sizing: border-box;
}

body {
  background-color: #141d2b;
  color: #181a1e;
  font-family: 'Secular One', Arial, Helvetica, sans-serif;
}

.App {
  text-align: center;
}

.mainContainer {
  max-width: 1200px;
  width: 95%;
  border-radius: 5px;

  background-color: #111927;
  padding: 1em;
  border-radius: 0%;
  box-shadow:
  5px 0 40px #9fef00;         /* outer right cyan */

  margin: 0 auto;
}

.cardHeader {
  position: relative;
}
.cardTitle {
  color: #9fef00;
  font-size: 2.5em;
  text-transform: uppercase;
  margin: 0 0 0.25em;
  letter-spacing: 0.15em;
}
.resetButton {
  font-family: 'Secular One', Arial, Helvetica, sans-serif;
  font-size: 1.5em;
  outline: none;
  border: none;
  background-color: #9fef00;
  color: #f3f5f2;
  margin-bottom: 0.5em;
  position: absolute;
  right: 0;
  top: 0;
}

@media screen and (max-width: 800px) {
  .resetButton {
    position: relative;
  }
}
.bingoSquare {
    background-color: #f3f5f2;
    position: relative;
}

.taskTextContainer {
    position: relative;
    z-index: 2;
    cursor: pointer;
}

.taskTextContainer:hover {
    color: #1d3557;
}

.taskLinkContainer {
    width: 100%;
    align-self: flex-end;
    display: block;
}
.taskLink {
    position: relative;
    text-decoration: none;
    color: #457b9d;
    text-transform: uppercase;
    padding: 2px 10px;
    transition: 0.1s;
    display: inline-block;
}

.taskLink:hover {
    color: #e63946
}
.taskLink:before, .taskLink:after {
    transition: 0.1s;
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    border: 3px solid #a8dadc;
    width: 0.25em;
}
.taskLink:before {
    left: 0;
    border-right: 0px;
}
.taskLink:after {
    right: 0;
    border-left: 0px;
}

.taskLink:hover:before, .taskLink:hover:after {
    border-color: #457b9d;
}

.BingoCard {
    display: grid;
    grid-template-rows: repeat(5, 1fr);
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 10px;
}

@media screen and (min-width: 801px) {
    .bingoSquare {    
        display: flex;
        padding: 0.5em 1em;
        flex-wrap: wrap;
        align-items: center;
        flex-direction: column;
        justify-content: space-evenly;
        overflow: hidden;
    }


    .bingoSquare:before {
        display: block;

        content: "\2714";
        color: #9fef00;

        position: absolute;
        text-align: center;
        pointer-events: none;
        left: 0;
        right: 0;
        top: 50%;
        transform: translate(0, -150%);
        font-size: 8em;
        opacity: 0.9;           
        transition: 0.1s;
    }
    .bingoSquare--checked:before {
        transform: translate(0, -50%);
    }
    
    .bingoCheckbox {
        display: none;
    }

    .taskTextContainer {
        display:flex;
        align-items: center;
        flex-grow: 1;
    }
    .taskText--free {
        font-size: 3em;
    }
}

@media screen and (max-width: 800px) {
    
    .BingoCard {
        grid-template-rows: repeat(24, auto);
		grid-template-columns: auto;
    }

    .bingoSquare {
        display: grid;
        grid-template-columns: 25px auto;

        grid-gap: 0.5em;
        text-align: left;
        align-items: flex-start;
        padding: 0.5em;
    }

    .taskLinkContainer {
        width: auto;
        grid-column: 2;
    }

    .taskLink {
        display: inline-block;
    }

    .taskText--vote {
        font-size: 1.5em;
        line-height: 1;
    }

    .taskTextContainer {
        grid-column: 2;
        grid-row: 1;
    }

    .bingoCheckbox {
        border: 2px solid #457b9d;
        position: relative;
        padding-bottom: 80%;
        font-size: 1em;
        opacity: 1;
    }

    .bingoSquare--checked .bingoCheckbox:before {
        display: block;
        content: "\2714";
        color: #9fef00;
        position: absolute;
        top: -2px;
        left: 0;
        right: 0;
        text-align: center;
        font-size: 1.2em;
    }
}
.Header {
    font-family: 'Fredoka', sans-serif;
    color: #9fef00;
    font-size: 3em;
    text-transform: uppercase;
}

.Header .aside {
    color: #ff3e3e;
}


@media screen and (max-width: 800px) {
    .Header {
        
        font-size: 1.5em;
    }
}
.Footer a {
    /* text-decoration: none; */
    color: #457b9d;
}
.Footer a:hover {
    color: #1d3557;
}
